<template>
  <div>
    <div class="section-padding">
      <a name="blog"></a>
      <div class="section-header-padding-top"></div>
      <h1 class="text-center section-header">Captain's Blog</h1>
      <div class="section-header-underline"></div>
      <div class="section-header-padding-bottom"></div>
      <div class="row section-content-container">
        <div class="col-md-12 col-sm-12 col-xs-12">
          <!-- <b-form>
            <b-form-group>
              <b-form-input
                type="text"
                v-model="title"
                placeholder="New Blog Title"
              ></b-form-input>
            </b-form-group>        
            <b-form-group>
              <b-form-textarea
                id="textarea"
                v-model="body"
                rows="3"
              ></b-form-textarea>
            </b-form-group>   
            <b-button type="submit" @click.prevent="addPost()" variant="primary">Submit</b-button>
          </b-form> -->
          <div class="text-container text-center" v-for="post in posts" v-bind:key="post._id">
              <h3>{{post.title}}</h3>
              <p>{{moment(post.createdAt)}}</p>
              <p>{{post.body}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../services/PostService'
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'Blog',
  data() {
    return {   
      posts: [],
      title: "",
      body: ""
    }
  },
  mounted () {
    var that = this;
    axios.get("/api/posts/").then(function(res) {
      let data = res.data.posts
      function compare(a, b) {
          if (a.createdAt > b.createdAt)
            return -1;
          if (a.createdAt < b.createdAt)
            return 1;
          return 0;
        }   
        that.posts = data.sort(compare); 
    }).catch(function(err) {
      console.log(err)
    })
  },
  methods: {
    addPost() {
      PostService.addPost(this.title, this.body).then(function() {
        window.location = "/";
        return;
      });
    },
    moment(timestamp) {
      return moment(timestamp).format('MMMM Do YYYY, h:mm a');
    }
  }
}
</script>